.wrap{
    padding-left: 20px;
    padding-right: 20px;
    width: 320px;
 margin: 0 auto;
    
}

 .list {
     margin-bottom: -30px;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
 }

 .item {
     margin-bottom: 30px;
     background-color: var(--text-secondary-color);
     width: 280px;
     height: 370px;
    position: relative;
    box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%), 0px 2px 1px rgb(0 0 0 / 20%);
     transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
 }

 .item:hover,
  .item:focus {
  transform: scale(1.03);
  
 }
 .wrapImg{
    width: 280px;
    height: 280px;
    position: relative;
  
 }


 .img {
    width: 100%;
    height: 100%;
    object-fit: cover;
   
 }
 .hoverImg{
    width: 100%;
     height: 100%;
     background-color: rgba(4,4,4,.5);
     display: flex;
     justify-content: center;
     align-items: center;
    opacity: 0;
     position: absolute;
     top: 0;
     transition: opacity .3s 250ms cubic-bezier(0.4, 0, 0.2, 1) ;
 }
 .hoverImg:hover{
opacity: 1;
 }
 .svg {
     fill: var(--input-border-color);
 }

 .info {
     padding: 20px;
    
 }

 .title {
     font-weight: 700;
     font-size: 12px;
     line-height: 12px;
     margin-bottom: 10px;
 }

 .role {
     font-size: 12px;
     line-height: 12px;
     color: var(--text-third-color);
 }

 .link {
    display: flex;
    align-items: center;
    justify-content: center;
 }
 .linkedin{
    margin-right: 15px;
 }
 
    
 

 @media screen and (min-width:768px) {
.wrap {
        width: 740px;
        padding-left: 32px;
        padding-right: 32px;
    }
     .list {
         display: flex;
         flex-wrap: wrap;
         flex-direction: row;
         margin-left: -30px;
     }

     .item {
         width: calc(100% / 2 - 30px);
         margin-left: 30px;
     }

         .wrapImg {
         width: 100%;

     }

     .info {
         padding-right: 28px;
     }
     .link{
left: 235px;
     }

 }

 @media screen and (min-width:1280px) {
.wrap {
        width: 890px;
    }
     .item {
         width: calc(100% / 3 - 30px);
     }
     .link{
    left: 170px;
     }
 }